@-webkit-keyframes pulsebg {
  0% {
    background-color: rgba(0, 0, 0, 0.05); }

  100% {
    background-color: rgba(0, 0, 0, 0.4); } }
@-moz-keyframes pulsebg {
  0% {
    background-color: rgba(0, 0, 0, 0.05); }

  100% {
    background-color: rgba(0, 0, 0, 0.4); } }
@-o-keyframes pulsebg {
  0% {
    background-color: rgba(0, 0, 0, 0.05); }

  100% {
    background-color: rgba(0, 0, 0, 0.4); } }
@keyframes pulsebg {
  0% {
    background-color: rgba(0, 0, 0, 0.05); }

  100% {
    background-color: rgba(0, 0, 0, 0.4); } }

