.rescue-dog--header {
  margin: 0;
  padding: 0;
  font: 2em/1 "Garamond", serif;
  text-shadow: 2px 2px rgb(0 0 0 / 80%);
}

.rescue-dog--header a:link {
  color: #990000;
  text-decoration: none;
}

.rescue-dog--header a:hover {
  text-decoration: underline;
}

.rescue-dog {
  position: relative;
  overflow: hidden;
  color: white;
  background: none no-repeat center center;
  text-shadow: 1px 1px rgb(0 0 0 / 80%);
  font: 28px/1.4 "helvetica neue", helvetica, sans-serif;
  font-weight: 200;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -ms-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.rescue-dog--breed,
.rescue-dog--vitals {
  font: 1.5em/0.8 "helvetica neue", helvetica, sans-serif;
  font-weight: 200;
  text-shadow: 2px 2px rgb(0 0 0 / 80%);
}

.rescue-dog--actions {
  position: absolute;
  right: 50px;
  bottom: 50px;
}

.rescue-dog--caption {
  position: absolute;
  top: 25px;
  padding: 25px;
  -webkit-animation-name: pulsebg;
  -moz-animation-name: pulsebg;
  -ms-animation-name: pulsebg;
  -o-animation-name: pulsebg;
  animation-name: pulsebg;
  -webkit-animation-duration: 6s;
  -moz-animation-duration: 6s;
  -ms-animation-duration: 6s;
  -o-animation-duration: 6s;
  animation-duration: 6s;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  -ms-animation-iteration-count: infinite;
  -o-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
  -moz-animation-direction: alternate;
  -ms-animation-direction: alternate;
  -o-animation-direction: alternate;
  animation-direction: alternate;
  -webkit-animation-timing-function: ease-in-out;
  -moz-animation-timing-function: ease-in-out;
  -ms-animation-timing-function: ease-in-out;
  -o-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
}

.rescue-dog--caption__left  {
  left: 25px;
  text-align: left;
}

.rescue-dog--caption__right  {
  right: 25px;
  text-align: right;
}

.rescue-dog--caption a:link {
  color: #990000;
  text-decoration: none;
}
.rescue-dog--caption a:visited {
  color: #990000;
  text-decoration: none;
}
.rescue-dog--caption a:hover {
  color: #990000;
  text-decoration: underline;
}
.rescue-dog--caption a:active {
  color: #990000;
  text-decoration: none;
}


@media (max-width: 767px) {
  .rescue-dog {
    font-size: 14px;
    height: 512px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .rescue-dog {
    font-size: 16px;
    height: 662px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .rescue-dog {
    font-size: 18px;
    height: 800px;
  }
}
@media (min-width: 1200px) and (max-width: 1919px) {
  .rescue-dog {
    font-size: 24px;
    height: 1278px;
  }
}
